#root{
    min-height:100%;
    display: flex;
    flex-direction: column;
}

/* 修改 for-editor中的全屏样式 */

.for-container{
    height:auto !important;
    font-size:16px !important;
    min-height:100%;
}
.exam-product .ant-btn-primary {
    padding: 0 40px !important;
    border-radius: 4px !important;
    border: 0 !important;
    font-size: 14px !important;
    color: #fff !important;
    background: linear-gradient( -90deg,rgba(78,117,255,1) 0%,rgba(1,57,253,1) 100%) !important;
}
.exam-product .ant-modal-footer {
    border: 0;
    text-align: center;
    padding: 40px 0;
}